import React, { useState } from 'react'
import ReactDOM from 'react-dom'
import {
    BrowserRouter as Router,
    Switch, Route, Link
} from "react-router-dom"
import "./assets/main.css"
import hamburgermenuicon from "./hamburgermenu.svg"
import Unity, {UnityContext} from "react-unity-webgl";
import YeetYourselfMainImage from "./images/YeetYourselfMain.PNG"
import SpaceInvadersMainImage from "./images/SpaceInvadersMainImage.PNG"
import ProceduralGenerationMainImage from "./images/ProceduralGenerationMainImage.PNG"



